<template>
    <div class="container">
        <div class="mine" @click="openSecret">
            <img src="@/assets/images/mine_1.png" />
            <span class="username">{{userName}}</span>
            <span class="money">{{money}}</span>
        </div>
        <g-password :show="passwordShow" @cancel="cancel" @confirm="confirm"/>
    </div>
</template>
<script>
import { mapGetters } from 'vuex';
export default{
    data(){
        return{
            passwordShow:false
        }
    },
    computed: {
        ...mapGetters(['userName','money']),
    },
    methods:{
        openSecret(){
            this.passwordShow = true
        },
        cancel(){
            this.passwordShow = false
        },
        confirm(res){
            if(res == '123456789'){
                this.$router.push('/setting');
            }else{
                this.passwordShow = false
                this.$toast.error('密码错误')
            }
        }
    }
}
</script>
<style scoped>
.container{
    margin: 0;
    padding: 0;
}
.mine{
    position: relative;
    height: calc(100vh - 55px);
    background-color: #fff;
    display: block;
}
.mine>img{
    height: 635px;
    max-width: 500px;
    width: 100%;
    pointer-events: none;
}
.username{
    display: block;
    position: absolute;
    z-index: 99;
    font-size: 19px;
    top: 83px;
    left: 83px;
    font-weight: 700;
    color: #333;
}
.money{
    display: block;
    position: absolute;
    z-index: 99;
    font-size: 15px;
    top: 191px;
    left: 83px;
    color: #ddd;
}
</style>